import React from "react";
import {
    Card,
    CardBody
} from "reactstrap";
import Table from "./components/table";
import Toolbar from "./components/toolbar";
import Pagination from "./components/pagination";

export default function Batches() {
    return (
        <React.Fragment>
            <Toolbar/>
            <Card>
                <CardBody className="py-2">
                    <Pagination>
                        <Table/>
                    </Pagination>
                </CardBody>
            </Card>
        </React.Fragment>
    );
}
